/* CSS Custom Properties Definitions */
:root {
    --maxWidth-none: 'none';
    --maxWidth-xs: 20rem;
    --maxWidth-sm: 24rem;
    --maxWidth-md: 28rem;
    --maxWidth-lg: 32rem;
    --maxWidth-xl: 36rem;
    --maxWidth-2xl: 42rem;
    --maxWidth-3xl: 48rem;
    --maxWidth-4xl: 56rem;
    --maxWidth-full: '100%';
    --maxWidth-wrapper: var(--maxWidth-2xl);
    --spacing-px: '1px';
    --spacing-0: 0rem;
    --spacing-1: 0.25rem;
    --spacing-2: 0.5rem;
    --spacing-3: 0.75rem;
    --spacing-4: 1rem;
    --spacing-5: 1.25rem;
    --spacing-6: 1.5rem;
    --spacing-8: 2rem;
    --spacing-10: 2.5rem;
    --spacing-12: 3rem;
    --spacing-16: 4rem;
    --spacing-20: 5rem;
    --spacing-24: 6rem;
    --spacing-32: 8rem;
    //   --fontFamily-sans: Montserrat, system-ui, -apple-system, BlinkMacSystemFont,
    //     "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif,
    //     "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    //   --fontFamily-serif: "Merriweather", "Georgia", Cambria, "Times New Roman",
    //     Times, serif;
    --font-body: var(--fontFamily-serif);
    --font-heading: var(--fontFamily-sans);
    --fontWeight-normal: 400;
    --fontWeight-medium: 500;
    --fontWeight-semibold: 600;
    --fontWeight-bold: 700;
    --fontWeight-extrabold: 800;
    --fontWeight-black: 900;
    --fontSize-root: 16px;
    --lineHeight-none: 1;
    --lineHeight-tight: 1.1;
    --lineHeight-normal: 1.5;
    --lineHeight-relaxed: 1.625;
    /* 1.200 Minor Third Type Scale */
    --fontSize-0: 0.833rem;
    --fontSize-1: 1rem;
    --fontSize-2: 1.2rem;
    --fontSize-3: 1.44rem;
    --fontSize-4: 1.728rem;
    --fontSize-5: 2.074rem;
    --fontSize-6: 2.488rem;
    --fontSize-7: 2.986rem;
    --color-primary: #005b99;
    --color-text: #2e353f;
    --color-text-light: #4f5969;
    --color-heading: #1a202c;
    --color-heading-black: black;
    --color-accent: #3d5c75;
}

/* HTML elements */

* {
    box-sizing: border-box;
}

html,
body {
    height: 100%;
    padding: 0;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    display: flex;
    min-height: 100%;
    flex-direction: column;
    background: rgb(232, 232, 232);
}

footer {
    margin-top: auto;
}

.cv-section {
    .cv-section-title {
        border-bottom: 1px solid black;
    }

    table > tr {
        padding-top: var(--spacing-1);
        padding-bottom: var(--spacing-1);
    }

    table td {
        padding: var(--spacing-2);
        vertical-align: top;
    }
}

table {
    width: 100%;
    margin-bottom: var(--spacing-8);
    border-collapse: collapse;
    border-spacing: 0.25rem;
}

table thead tr th {
    border-bottom: 1px solid var(--color-accent);
}

/* Images that appear on projects and graphic design pages */
.project-image {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
        max-width: 375px;
    }
}

.project-description {
    display: flex;
    align-items: center;
    // justify-content: left;
}

.footer-content {
    border-top: 1px solid black;
}

.university-logo {
    max-height: 100%;
    aspect-ratio: 1;
    max-width: 72px;
    object-fit: cover;
}

hr {
    background: var(--color-accent);
    height: 1px;
    border: 0;
}

.jumbotron {
    background-color: rgb(211, 216, 226);
    padding-top: 16px;
    padding-bottom: 16px;
}

/* Heading */

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--font-heading);
    margin-bottom: var(--spacing-6);
    line-height: var(--lineHeight-tight);
    letter-spacing: -0.025em;
}

h2,
h3,
h4,
h5,
h6 {
    font-weight: var(--fontWeight-bold);
    color: var(--color-heading);
}

h1 {
    font-weight: var(--fontWeight-black);
    font-size: var(--fontSize-6);
    color: var(--color-heading-black);
}

h2 {
    font-size: var(--fontSize-5);
}

h3 {
    font-size: var(--fontSize-4);
}

h4 {
    font-size: var(--fontSize-3);
}

h5 {
    font-size: var(--fontSize-2);
}

h6 {
    font-size: var(--fontSize-1);
}

h1 > a {
    color: inherit;
    text-decoration: none;
}

h2 > a,
h3 > a,
h4 > a,
h5 > a,
h6 > a {
    text-decoration: none;
    color: inherit;
}

/* Prose */

p {
    line-height: var(--lineHeight-relaxed);
    --baseline-multiplier: 0.179;
    --x-height-multiplier: 0.35;
    margin: var(--spacing-0) var(--spacing-0) var(--spacing-8) var(--spacing-0);
    padding: var(--spacing-0);
}

/* Blog Posts */

.blog-post header h1 {
    margin: var(--spacing-0) var(--spacing-0) var(--spacing-4) var(--spacing-0);
}

.blog-post header p {
    font-size: var(--fontSize-2);
    font-family: var(--font-heading);
}

blockquote {
    color: var(--color-text-light);
    margin-left: calc(-1 * var(--spacing-6));
    margin-right: var(--spacing-8);
    padding: var(--spacing-0) var(--spacing-0) var(--spacing-0) var(--spacing-6);
    border-left: var(--spacing-1) solid var(--color-primary);
    font-size: var(--fontSize-2);
    font-style: italic;
    margin-bottom: var(--spacing-8);
}

blockquote > :last-child {
    margin-bottom: var(--spacing-0);
}

blockquote > ul,
blockquote > ol {
    list-style-position: inside;
}

/* Media queries */

@media (max-width: 42rem) {
    blockquote {
        padding: var(--spacing-0) var(--spacing-0) var(--spacing-0)
            var(--spacing-4);
        margin-left: var(--spacing-0);
    }

    ul,
    ol {
        list-style-position: inside;
    }
}

.homepage-jumbotron {
    background: url('/static/images/banner.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    padding-top: 3rem;
    background-color: black;

    .jumbotron-content {
        background-color: rgba(18, 18, 18, 0.9);
        border-radius: 8px;
        color: white;
        border: hsl(54, 80%, 50%) solid 2px;
        padding: 1.5rem;
    }

    .display-1 {
        color: white;
    }
}

.custom-nav {
    background: #1a202c;
}

footer {
    background: #1a202c;
    color: white;
}

.w-20 {
    width: 20%;
}

.w-80 {
    width: 80%;
}
